.hero {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10rem;
  max-width: 68.75rem;
  margin: auto;
  padding: 10vh 3vw;
}
.hero img {
  height: 25rem;
  width: 25rem;
}

.bio {
  width: 25rem;
  padding: 0.625rem;

  border-radius: 6px;
  box-shadow: 0px 2px 15px 2px var(--primary-shadow);
}

.bio h1 {
  margin-bottom: var(--bottom-margin);
}

.bio p {
  line-height: var(--line-height);
  padding: 0.3rem 0;
}
