.top-container {
  background: url("/src/assets/images/space_1.jpg") center center/cover
    no-repeat;
  height: 70vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: left;
  padding-left: 20vw;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  object-fit: contain;
}

.top-container > h1 {
  color: #fff;
  font-size: 100px;
  margin-top: -100px;
}

.typing {
  margin-top: 8px;
  color: #fff;
  font-size: 50px;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}

@media screen and (max-width: 960px) {
  .top-container > h1 {
    font-size: 70px;
    margin-top: -150px;
  }
}

@media screen and (max-width: 768px) {
  .top-container > h1 {
    font-size: 50px;
    margin-top: -100px;
  }

  .top-container > p {
    font-size: 30px;
  }
}

.topanchor {
  display: block;
  position: relative;
  top: -250px;
  visibility: hidden;
}
